import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: resolve => require(['./views'], resolve)
    },
    {
      path: 'page1',
      name: 'page1',
      component: resolve => require(['./views/page1'], resolve),
      children: [
        {
          path: '/home',
          name: 'page1_home',
          component: resolve => require(['./views/page1/home'], resolve)
        }
      ]
    }
  ]
})
